class TypeList extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      title: '',
      list: []
    }
    this.reload = this.reload.bind(this)
    // this.fnPageInit=this.fnPageInit.bind(this)
  }
  componentDidMount() {
    methods.getPageQuery(()=>this.fnPageInit(), this)
  }

  fnPageInit() {
    let { query } = this;
    // console.log('query',query)
    let pageData = setData(query.type)
    // console.log('pageData', pageData)

    this.setState({
      title: pageData.name,
      list: pageData.list
    })
  }

  clickGoHome() {
    methods.goLink('index')
  }

  clickRowHandle(item) {
    let { link } = item;
    if(link){
      methods.goLink(link, `?type=${item.type}&name=${encodeURIComponent(item.name)}&msg=${encodeURIComponent(item.msg)}&img=${item.img}`)
    }
  }

  reload() {
    // this.onLoad();
    // console.log('location', location)
    // window.location.reload()
  }

  fnWraperLoaded() {
    // Wraper
    console.log('wraper load')
  }

  render() {
    let { title, list } = this.state
    // console.log('list', list)
    return (
      <Wraper
        title={title}
        classN="page_typelist"
        loadend={this.fnWraperLoaded}
      >
        {/* <div onClick={this.clickGoHome}></div> */}
        {/* <br /> */}
        {/* <div onClick={this.reload}></div> */}
        <ul>
          {list &&
            list.length > 0 &&
            list.map((item, i) => {
              return (
                item.isShow &&  <ListRow
                  key={i}
                  clickRowHandle={()=>this.clickRowHandle(item)}
                  item={item}
                  classN="movement_list"
                /> 
              )
            })}
        </ul>
      </Wraper>
    )
  }
}

// export default TypeList

export default function createApp() {
  const container = document.createElement('div')
  container.id = 'app'
  document.body.appendChild(container)
  ReactDOM.render(<TypeList />, container)
}

methods.isMiniprogram || createApp()